import React from 'react'
import { BiRadioCircle } from 'react-icons/bi'

const ExperienceCard = (props) => {
    const { img, title, period, accomplishments } = props
    return (
        <div className='my-1'>
            <div className='fl'>
                <div className='px-1'>
                    <img height={100} width={100} src={img.src} alt={img.alt} />
                </div>
                <div>
                    <h2 className='fs-5 fw-bold mb-1'>{title}</h2>
                    <div className='text-secondary mb-1'>{period}</div>
                </div>
            </div>
            <ul className='list-unstyled text-secondary fs-14px mx-1'>
                {accomplishments.map((text, index) => (
                    <li key={index} className='position-relative px-1'>
                        <BiRadioCircle className='text-primary list-radio position-absolute left' />
                        <p>{text}</p>
                    </li>
                ))}
            </ul>
        </div>
    )
}

export default ExperienceCard