import React, { useState } from 'react'
import StatusModal from '../../../components/UI/StatusModal/StatusModal'
import Spinner from '../../../components/UI/Spinner/Spinner'
import './Contact.css'
import axios from 'axios'

// Email Service uses EmailJS using nicyzk2@gmail.com account.

const Contact = () => {

    const [name, setName] = useState('')
    const [email, setEmail] = useState('')
    const [subject, setSubject] = useState('')
    const [description, setDescription] = useState('')
    const [modalDet, setModalDet] = useState({ show: false, status: '', message: '' })
    const [loading, setLoading] = useState(false)

    const onSubmit = (e) => {
        e.preventDefault()
        setLoading(true)
        axios.post('https://api.emailjs.com/api/v1.0/email/send', {
            service_id: 'service_hv4fg7m',
            template_id: 'template_yyhfe0j',
            user_id: 'user_4EzM80j3jGnwnl2Ozx1eG',
            template_params: { name, email, subject, description }
        })
        .then(res => {
            setName('')
            setEmail('')
            setSubject('')
            setDescription('')
            setLoading(false)
            setModalDet( { show: true, status: 'success', message: 'Your message has been sent. Thank you for your time!' } )
        })
        .catch(err => {
            setLoading(false)
            setModalDet( { show: true, status: 'error', message: err.message } )
        })
        
    }

    return (
        <section id='contact' className='bg-lavender px-1 py-6 px-md-4 px-xl-10'>

            {/* Note: Section cannot be removed during re-renders due to event listener spyScroll. Else, won't work properly */}
            { modalDet.show ? <StatusModal modalDet={modalDet} setModalDet={setModalDet} />: null }
            { loading ? <Spinner/>: null}

            <div className='fl fl-col contact-fl-lg-row contact-fl-lg-space-even'>
                <div className='text-center prompt-w-90 prompt-w-sm-70 prompt-w-lg-40 p-1 mx-auto mb-3'>
                    <h2 className='fs-3 fw-bold'>Have a project in mind?</h2>
                    <p className='fs-5'>
                        Hey there! I'm always open to new ideas and opportunities! If you have anything you want to discuss about
                        please feel free to reach out and connect with me!
                    </p>
                </div>
                <div className='bg-white shadow px-2 form-w mx-auto'>
                    <h2 className='fs-3 my-2 text-center'>Let's get in touch!</h2>
                    <form onSubmit={onSubmit}>
                        <div className='my-1'>
                            <label className='form-label fs-14px' htmlFor='name'>Name</label>
                            <input className='form-control' type='text' id='name' placeholder='Name'
                                value={name} onChange={e => setName(e.target.value)} required></input>
                        </div>
                        <div className='my-1'>
                            <label className='form-label fs-14px' htmlFor='email'>Email</label>
                            <input className='form-control' type='email' id='email' placeholder='Email address'
                                value={email} onChange={e => setEmail(e.target.value)} required></input>
                        </div>
                        <div className='my-1'>
                            <label className='form-label fs-14px' htmlFor='subject'>Subject</label>
                            <input className='form-control' type='text' id='subject' placeholder='Subject'
                                value={subject} onChange={e => setSubject(e.target.value)} required></input>
                        </div>
                        <div className='my-1'>
                            <label className='form-label fs-14px' htmlFor='description'>Description</label>
                            <textarea className='form-control' id='description' placeholder='Description'
                                value={description} onChange={e => setDescription(e.target.value)} required></textarea>
                        </div>
                        <button type='submit' className='btn btn-primary br-5px w-100 my-1'>Send Message</button>
                    </form>
                </div>
            </div>
        </section>
    )
}

export default Contact