import React from 'react'
import profilePic from '../../../assets/img/profile-pic.jpg'
import resume from '../../../assets/pdf/nicholas_yap_resume.pdf'
import { IoMailSharp, IoLogoLinkedin, IoLogoGithub } from 'react-icons/io5'
import './Masthead.css'

const MastHead = () => {
    return (
        <section id='home' className='bg-light-blue-diag min-vh-80 fl fl-center-y px-1 py-3 px-md-4 px-lg-6 px-xl-10' >
            <div className='fl fl-wrap fl-justify-center max-w-800px mx-auto'>
                <div className='w-md-20 mb-3'>
                    <img className='profile-pic' alt='profile pic' src={profilePic} />
                </div>
                <div className='text-center text-md-left w-md-80 fl fl-col fl-justify-center'>
                    <span className='fs-1 fw-bold mb-1'><h1>Hello, I'm Nicholas Yap!</h1></span>
                    <div className='fs-5'>
                        <p>
                            I'm a first year student in Renaissance Engineering Program (REP) in Nanyang Technological University,
                            with a specialisation in Computer Science.
                        </p>
                    </div>
                    <div className='my-2 fl fl-col fl-justify-between fl-md-row-reverse'>
                        <div className='fl mx-auto mb-3'>
                            <a className='mx-1' href="mailto:nicyzk@gmail.com" style={{ color: "orange" }}><IoMailSharp size={30} /></a>
                            <a className='mx-1' href="https://www.linkedin.com/in/nicholas-yap-5706311b7/" style={{ color: "#2867B2" }}><IoLogoLinkedin size={30} /></a>
                            <a className='mx-1' href="https://github.com/Nicyzk" style={{ color: "purple" }}><IoLogoGithub size={30} /></a>
                        </div>
                        <a href={resume} target='_blank' rel='noreferrer'><button className='btn btn-dark'>Resume</button></a>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default MastHead