import React from 'react'
import { BiRadioCircle, BiLinkExternal } from 'react-icons/bi'
import './ProjectCard.css'

const ProjectCard = (props) => {
    const { img, title, features, link } = props

    return (
        <div className='br-5px bg-white shadow overflow-hidden fl fl-col fl-justify-between'>
            <div>
                <div className='imgContainer border-btm-light'><img src={img.src} alt={img.alt} /></div>
                <div className='p-1 text-center'>
                    <a href={link} role='button' target='_blank' rel="noreferrer" >
                        <h3 className='fs-5 fw-bold py-1 text-hover-purple'>{title} <BiLinkExternal className='link-icon'/></h3>
                    </a>
                    <ul className='list-unstyled text-secondary fs-14px'>
                        {features.map((text, index) => (
                            <li key={index} className='position-relative px-1'>
                                <BiRadioCircle className='text-primary list-radio position-absolute left' />
                                <p>{text}</p>
                            </li>
                        ))}
                    </ul>
                </div>
            </div>
            {/* <button className='btn btn-dark mb-1 align-self-center'>Repository <IoLogoGithub className='icon' /></button> */}
        </div>


    )
}

export default ProjectCard