const closeSliderOnLinkClicked = (setIconClicked) => {
  const navLinks = document.querySelectorAll('.nav-link');
  Array.from(navLinks).forEach(n => {
    const onLinkSelect = e => {
      let x = window.matchMedia("(max-width: 768px)")
      if (x.matches) {
        const icon = document.getElementById('toggle-menu-icon')
        icon.classList.toggle('opened')
        icon.setAttribute('aria-expanded', icon.classList.contains('opened'))
        setIconClicked(false)
      }
    }
    n.addEventListener('click', onLinkSelect)
    n.addEventListener('touchstart', onLinkSelect)
  })

}

const makeNavLinksSmooth = () => {
  const navLinks = document.querySelectorAll('.nav-link');
  Array.from(navLinks).forEach(n => {
    const onLinkSelect = e => {
      e.preventDefault()
      const yoffSet = 50
      let pos = document.querySelector(n.hash).offsetTop
      pos -= yoffSet
      window.scrollTo({ top: pos, behavior: "smooth" })
    }
    n.addEventListener('click', onLinkSelect)
    n.addEventListener('touchstart', onLinkSelect)
  })
}

const spyScrolling = () => {
  const sections = document.querySelectorAll('section');

  window.onscroll = () => {
    let scrollPos = document.documentElement.scrollTop || document.body.scrollTop;
    scrollPos += 150
    Array.from(sections).forEach(s => {
      if (s.offsetTop <= scrollPos) {
        const id = s.id
        document.querySelector('.active').classList.remove('active');
        document.querySelector(`a[href*=${id}]`).classList.add('active');
      }
    })
  }
}


export { closeSliderOnLinkClicked, makeNavLinksSmooth, spyScrolling }
