import React, { useEffect, useState } from 'react'
import MenuIcon from './MenuIcon/MenuIcon'
import MenuSlider from './MenuSlider/MenuSlider'
import { closeSliderOnLinkClicked, makeNavLinksSmooth, spyScrolling } from './scrollspy'
import './Header.css'

const Header = () => {
    const [iconClicked, setIconClicked] = useState(false)

    useEffect(() => {
        closeSliderOnLinkClicked(setIconClicked)   // Only if media < md
        makeNavLinksSmooth()
        spyScrolling()
    }, [])

    return (
        <>
            <nav className='navbar bg-white shadow p-1 fl fl-justify-between fl-center-y'>
                <a href='/' className='navbar-brand'>Nicholas Yap</a>
                <div className='navbar-nav-md'>
                    <a href="#home" className='active nav-link fl fl-center-y mx-1'>Home</a>
                    <a href="#projects" className='nav-link fl fl-center-y mx-1'>Projects</a>
                    <a href="#education" className='nav-link fl fl-center-y mx-1'>Education</a>
                    <a href="#experience" className='nav-link fl fl-center-y mx-1'>Experience</a>
                    <a href="#contact" className='nav-link fl fl-center-y mx-1'>Contact</a>
                </div>
                <MenuIcon iconClicked={iconClicked} setIconClicked={setIconClicked} />
            </nav>
            <MenuSlider iconClicked={iconClicked}/>
        </>
    )
}


export default Header