import React from 'react'
import ntuLogo from '../../../assets/img/ntu-logo.png'
import ExperienceCard from '../Experience/ExperienceCard/ExperienceCard'
// Note: This container uses ExperienceCard from Experience section

const Education = () => {
    return (
        <section id='education' className='bg-white px-1 py-6'>
            <h1 className='fs-1 fw-bold text-center mb-3'>Education</h1>
            <div className='fl fl-col auto-w-centered'>
                <ExperienceCard
                    img={{ src: ntuLogo, alt: 'ntu logo'}}
                    title={'Nanyang Technological University, Renaissance Engineering Programme'}
                    period={'2021-2026'}
                    accomplishments={[
                        'REP is NTU’s flagship engineering program, characterized by award of a B. Eng. Sci. and a MSc in Technological Management in 4.5 years',
                        'Awarded NTU REP scholarship'
                    ]} />
            </div>
        </section>
    )
}

export default Education